/* MIXINS */
/* Animations SASS */
@-webkit-keyframes color-animate-from-blue {
  0% {
    color: #498dcb; }
  25% {
    color: #69387f; }
  50% {
    color: #b9234a; }
  75% {
    color: #7ec54a; }
  100% {
    color: #498dcb; } }

@-webkit-keyframes fill-animate-from-blue {
  0% {
    fill: #498dcb; }
  25% {
    fill: #69387f; }
  50% {
    fill: #b9234a; }
  75% {
    fill: #7ec54a; }
  100% {
    fill: #498dcb; } }

@-webkit-keyframes bgcolor-animate-from-gray {
  0% {
    background-color: #555555; }
  25% {
    background-color: #69387f; }
  50% {
    background-color: #b9234a; }
  75% {
    background-color: #7ec54a; }
  100% {
    background-color: #555555; } }

